#Navbar {
    background: linear-gradient(to bottom, #7F167F, #f56eb4d5);
    padding: 15px;
    color: white;
    position: fixed;
    width: 100%;
    box-shadow: 1px 1px 30px cyan;
    z-index: 1;
}

.Logo {
    border-radius: 16px;
    display: grid;
    grid-auto-flow: column;
    width: fit-content;
    place-items: center;
    gap: 15px;
    cursor: pointer;
    padding: 15px;
    transition: 0.5s;
    font-size: large;
}

.Logo:hover {
    box-shadow: 1px 1px 10px #99FEFF;
    transition: 0.5s;
}

.Logo img {
    width: 36px;
}